(function () {
  /**
     * dialog
     * ""
     * """/myProject/umeditor/"
     * UEditorURL
     * UEditorURL
     * window.UMEDITOR_HOME_URL = "/xxxx/xxxx/";
     */
  var URL = window.UMEDITOR_HOME_URL || (function () {
    function PathStack () {
      this.documentURL = self.document.URL || self.location.href

      this.separator = '/'
      this.separatorPattern = /\\|\//g
      this.currentDir = './'
      this.currentDirPattern = /^[.]\/]/

      this.path = this.documentURL
      this.stack = []

      this.push(this.documentURL)
    }

    PathStack.isParentPath = function (path) {
      return path === '..'
    }

    PathStack.hasProtocol = function (path) {
      return !!PathStack.getProtocol(path)
    }

    PathStack.getProtocol = function (path) {
      var protocol = /^[^:]*:\/*/.exec(path)

      return protocol ? protocol[0] : null
    }

    PathStack.prototype = {
      push: function (path) {
        this.path = path

        update.call(this)
        parse.call(this)

        return this
      },
      getPath: function () {
        return this + ''
      },
      toString: function () {
        return this.protocol + (this.stack.concat([''])).join(this.separator)
      }
    }

    function update () {
      var protocol = PathStack.getProtocol(this.path || '')

      if (protocol) {
        // 
        this.protocol = protocol

        // local
        this.localSeparator = /\\|\//.exec(this.path.replace(protocol, ''))[0]

        this.stack = []
      } else {
        protocol = /\\|\//.exec(this.path)
        protocol && (this.localSeparator = protocol[0])
      }
    }

    function parse () {
      var parsedStack = this.path.replace(this.currentDirPattern, '')

      if (PathStack.hasProtocol(this.path)) {
        parsedStack = parsedStack.replace(this.protocol, '')
      }

      parsedStack = parsedStack.split(this.localSeparator)
      parsedStack.length = parsedStack.length - 1

      for (var i = 0, tempPath, l = parsedStack.length, root = this.stack; i < l; i++) {
        tempPath = parsedStack[i]
        if (tempPath) {
          if (PathStack.isParentPath(tempPath)) {
            root.pop()
          } else {
            root.push(tempPath)
          }
        }
      }
    }

    var currentPath = document.getElementsByTagName('script')

    currentPath = currentPath[ currentPath.length - 1 ].src

    return new PathStack().push(currentPath) + ''
  })()

  /**
     * URL
     */
  window.UMEDITOR_CONFIG = {

    // 
    UMEDITOR_HOME_URL: URL,

    // 
    imageUrl: URL + 'php/imageUp.php', // 
    imagePath: URL + 'php/', // fixedImagePath,
    imageFieldName: 'upfile', // key,

    // new
    toolbar: [
      'source | fontfamily paragraph fontsize | undo redo | bold italic underline strikethrough | superscript subscript | forecolor backcolor | removeformat |',
      'insertorderedlist insertunorderedlist | selectall cleardoc',
      '| justifyleft justifycenter justifyright justifyjustify |',
      'link unlink | emotion image video  | map',
      '| horizontal print preview fullscreen', 'drafts', 'formula'
    ]

    // ,zh-cnlang
    // lang (navigator.language||navigator.browserLanguage ||navigator.userLanguage).toLowerCase()
    //, lang:"zh-cn"
    //, langPath:URL +"lang/"

    // ie
    //, autourldetectinie:false

    // ,defaultthemes
    // :default
    //, theme:'default'
    //, themePath:URL +"themes/"

    // getAllHtmlhead
    //, charset:"utf-8"

    // 
    //, isShow : true    //

    //, initialContent:'UMEDITOR!'    //,textarea/script

    //, initialFrameWidth:500 //,500
    //, initialFrameHeight:500  //,500

    //, autoClearinitialContent:true //focustrue,

    //, textarea:'editorValue' // namename

    //, focus:false //truefalse

    //, autoClearEmptyNode : true //getContentinlineElement

    //, fullscreen : false //

    //, readonly : false //,false

    //, zIndex : 900     //,900

    // p
    // .edui-editor-body .edui-body-containercss
    //, initialStyle:'.edui-editor-body .edui-body-container p{line-height:1em}'

    //, autoSyncData:true //

    //, emotionLocalization:false //emotionimages

    //, allHtmlEnabled:false //html

    // fontfamily
    // 
    //        ,'fontfamily':[
    //              { name: 'songti', val: ',SimSun'},
    //          ]

    // fontsize
    // 
    //, 'fontsize':[10, 11, 12, 14, 16, 18, 20, 24, 36]

    // paragraph
    //  
    //, 'paragraph':{'p':'', 'h1':'', 'h2':'', 'h3':'', 'h4':'', 'h5':'', 'h6':''}

    // undo
    // ,20
    //, maxUndoCount:20
    // 
    //, maxInputCount:1

    // imageScaleEnabled
    // ,true
    //, imageScaleEnabled:true

    // dropFileEnabled
    // ,true
    //, dropFileEnabled:true

    // pasteImageEnabled
    // QQ,true
    //, pasteImageEnabled:true

    // autoHeightEnabled
    // ,true
    //, autoHeightEnabled:true

    // autoFloatEnabled
    // toolbar,true
    //, autoFloatEnabled:true

    // 
    //, topOffset:30

    // 
    //, filterRules: {}
  }
})()



